import { Component, OnInit, ViewChild } from '@angular/core';
import { Card } from '../card.model';
import { NgForm } from '@angular/forms';
import { OperationService } from '../operations.service';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  sortedArr: any[];
  canShuffle = false;
  suiteCards: Card;
  totalCards = 52;
  noCards = false;
  randomSet = [];
  suiteArr: string[];
  cardArr: any[];
  displayCards = false;
  constructor(private opService: OperationService) {}
  suits = this.opService.suits;
  @ViewChild('f') myForm: NgForm;
  ngOnInit() {
    if (this.totalCards) {
      this.canShuffle = true;
    }
  }

  onDraw(form: NgForm) {
    const value = form.value;
    if (this.totalCards >= value.cards) {
      this.displayCards = true;
      this.randomSet = this.opService.getRandomSuite(value.cards);
      this.suiteArr = this.randomSet[0];
      this.cardArr = this.randomSet[1];
      this.sortedArr = this.opService.sortSuites(this.suiteArr, this.cardArr);
      console.log(this.sortedArr);
    } else {
      this.noCards = true;
      this.canShuffle = false;
    }
    this.totalCards -= value.cards;
  }


}
