import { Deck } from './deck.model';
import { Card } from './card.model';
import { ObjectUnsubscribedError } from 'rxjs';

export class OperationService {
    private deck: Deck = new Deck();
    suiteCards: Card;
    suite: string;
    number: any;
    suiteArr: string[];
    cardArr: any[];
    public sortedDeck = {club: [], spade: [], heart: [], diamond: []};
    suits =  Object.keys(this.sortedDeck);
    intRank: any[];
    stringRank: any[];
    tempArr: any[];
    order: { 'Ace': boolean; 'King': boolean; 'Queen': boolean; 'Jack': boolean; };
    sortedArr: any;
    random(index: number) {
        return Math.floor(Math.random() * index);
    }
    popSuite(suite: string) {
        delete this.deck.newDeck[suite];
    }
    popCard(index: any) {
        if (index > -1) {
          this.suiteCards.cards.splice(index, 1);
         // console.log(this.suiteCards.cards);
       }
    }
    getRandomSuite(count: number) {
        this.suiteArr = [];
        this.cardArr = [];
        for (let i = 0; i < count; i++) {
            this.suite = Object.keys(this.deck.newDeck)[this.random(Object.keys(this.deck.newDeck).length)];
            this.suiteCards = this.deck.newDeck[this.suite];
            this.suiteArr.push(this.suite);
            this.number = this.suiteCards.cards[this.random(this.suiteCards.cards.length)];
            this.cardArr.push(this.number);
            this.popCard(this.suiteCards.cards.indexOf(this.number));
            if (this.suiteCards.cards.length < 1) {
                this.popSuite(this.suite);
              }
        }
        return [this.suiteArr, this.cardArr];
    }
    sortSuites(suiteArr: string[], cardArr: any[]) {
        for (const item in suiteArr) {
            if (suiteArr) {
                this.sortedDeck[suiteArr[item]].push(cardArr[item]);
            }
        }
        this.sortedArr = [];
        for (let i = 0; i < this.suits.length; i++) {
            this.tempArr = this.sortedDeck[this.suits[i]];
            this.stringRank = [];
            this.intRank = [];
            if (this.tempArr.length) {
                this.order = {
                    'Ace': false,
                    'King': false,
                    'Queen': false,
                    'Jack': false
                };
                for (let j = 0; j < this.tempArr.length; j++) {
                    if (typeof(this.tempArr[j]) === 'string') {
                        this.order[this.tempArr[j]] = true;
                    } else {
                        this.intRank.push(this.tempArr[j]);
                    }
                }
               for (const item in this.order) {
                   if (this.order[item]) {
                    this.stringRank.push(item);
                   }
               }
                this.intRank.sort((a, b) => {
                    if (a > b) {
                        return -1;
                    } else {
                        return 1;
                    }
                });
                this.sortedArr.push([this.suits[i], ...this.stringRank, ...this.intRank]);
            }
        }
        return this.sortedArr;
    }
}
